// vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

// 全局注册
Vue.use(Vuex);

//创建store对象
const store = new Vuex.Store({
    // 状态数据
    state:{
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华' },
            {name:'share',desc:'分享' },
            {name:'ask',desc:'问答' },
            {name:'job',desc:'招聘' },
            {name:'dev',desc:'客户端测试' },
          ],
          // sctionsheet菜单
          unLoginedMoreMenus:{
            login:'登录'
          },
          loginedMoreMenus:{
            create:'发表新话题',
            center:'个人中心',
            msg:'消息通知',
            loginout:'登出'
            
          },
          // 登录状态
          isLogined:false,
          // 登录用户名
          loginname:'',
          // accesstoken
          accesstoken:'',
          // actionsheet显示与否
          isShowMoreMenus:false,
          // 登录id
          author_id:''
    },
    // 状态属性的计算属性
    getters:{
        moreMenus:function(state){
            if(state.isLogined){
              return state.loginedMoreMenus;
            }else{
              return state.unLoginedMoreMenus;
            }
          },
          getTopicDescByTab:function(state){
            return function(tab){
              for(var i=0;i<state.topics.length;i++){
                if(state.topics[i].name==tab){
                  return state.topics[i].desc;
                }
              }
            }
          }
    },
    // 修改同步数据的函数
    mutations,
    // 异步修改数据
    actions
})

// 对外输出
export default store;



