export default {
    name:'center',
      // 获取登录名 
    computed:{
            loginname:function(){
                // console.log(this.$store.state.loginname);
                return this.$store.state.loginname;
            }
        },
    created:function(){ 
        // 跳转到用户界面，使用编程式路由跳转
        // this.$router.push这个方法回想history添加一个记录，点击后退到上一个界面
        // this.$router.push('/user/'+this.loginname);
        // this.$router.replace('/user/'+this.loginname)，不会向history添加记录
        // 上一个页面的记录不存在
        
        this.$router.replace('/user/'+this.loginname);

    }
}



